@import url(https://fonts.googleapis.com/css2?family=Special+Elite&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App{width:950px;margin:0 auto;background:url(/memory-card/static/media/forest.db046080.jpeg);background-size:cover}ul{margin:0;padding-left:0;list-style:none}header{background-color:rgba(160,160,160,0.5);padding:5px;text-align:center}header h1{margin-top:0;margin-bottom:10px}header p{margin:0}header .scores{margin:20px}header li{display:inline;margin:0 1em;padding:.5em 1em;border:1px solid #ccc;border-radius:15px;font-weight:bold;font-feature-settings:"smcp";font-variant:small-caps}header span{margin-left:1em;font-size:1.2em;color:#ddd}.cards{display:grid;grid-template-columns:repeat(3, 1fr);grid-gap:20px;padding:20px 100px}.card{display:grid;justify-content:center;padding:20px;border:1px solid #ccc;border-radius:10px;background-color:rgba(255,255,255,0.5)}.card>div{height:150px;width:200px;border-radius:5px;background-position:center;background-size:cover}.card p{margin-top:10px;margin-bottom:0;margin-right:10px;text-align:right;font-size:26px;font-family:'Special Elite', cursive}.card:hover{cursor:pointer;background-color:rgba(255,255,255,0.4)}.card:active{scale:1.02;background-color:rgba(255,255,255,0.7)}._6 p{font-size:20px}

